<template>
  <div class="main">
    <div class="regtop">
      <div class="regtop-left" @click="goBack">
        <span class="iconfont icon-zuo"></span>
      </div>
      <div class="regtop-midd">会员注册</div>
      <div class="regtop-right"></div>
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[
          { required: true, message: '请填写用户名' },
          {
            pattern: /^[a-z_$][0-9a-z_$]{3,9}$/i,
            message: '不能以数字开头 由数字字母_$组成 4-10位',
          },
        ]"
      />
      <van-field
        v-model="password"
        type="password"
        name="userpwd"
        label="密码"
        placeholder="密码"
        :rules="[
          { required: true, message: '请填写密码' },
          {
            pattern: /^[0-9a-z]{5,11}$/i,
            message: '密码以数字字母组成 6-12位',
          },
        ]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit" color="#bd1a21"
          >注册</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import userModel from "@/model/userModel";
import mixin from "@/mixin";
export default {
  mixins:[mixin],
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    async onSubmit(info) {
      let { data: list } = await userModel.query({
        username: info.username,
      });
      if (!list.length) {
        await userModel.register({
          username: info.username,
          userpwd: info.userpwd,
        });
        this.$toast.success("注册成功");
        this.$router.push("/login")
      } else {
        this.$toast.fail("该用户名已注册");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  .regtop {
    width: 100%;
    height: 45px;
    background: #bd1a21;
    display: flex;
    text-align: center;
    color: #fff;
    line-height: 45px;
    .regtop-left {
      width: 45px;
      .icon-zuo {
        font-size: 16px;
        margin-left: 15px;
      }
    }
    .regtop-midd {
      font-size: 18px;
      flex: 1;
    }
    .regtop-right {
      width: 45px;
    }
  }
}
</style>